import React, {Fragment} from 'react'


export default function UserMessages({detal_user_info}) {


  return (
   <Fragment>
    <div className="user-messages">
     <img className='user-messages__avatar' src={detal_user_info.avatar} alt="" />
     <p>{detal_user_info.name}</p>
    </div>
  </Fragment>
  )
}