import React, { Fragment, useState } from 'react'
import AddBoxRoundedIcon from '@material-ui/icons/AddBoxRounded';

export default function ProjectInfo() {
 const [open, setOpen] = useState(false);

  const ShowInformation = () => {
    setOpen(true);
  };
  const HideInformation = () => {
   setOpen(false);
 };

  return (
   <Fragment>
    <div className="info-page__container"  >
      <p className='info-page__paragraph'>Hey! Thank for jumping on the project. So for today  we need to finish the Shareholders 
         Document Analysis. I have left all the key points to consider in the tasks tab. There are
         just two crucial.  
         {open === false?
          <a className="info-page__link" onClick={ShowInformation}>Read more</a>
         :
         <p onClick={HideInformation}>Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsa id impedit at dolorum, beatae necessitatibus corporis, 
           tempora sapiente quos officiis labore itaque aliquam debitis praesentium quam ex animi optio distinctio obcaecati,
           reprehenderit quisquam.</p>
         }
         </p>
         <button className='info-page__btn'>
           <AddBoxRoundedIcon className='info-page__btn__plus' />
          <p><b>Create a DELIVERABLE</b></p>
         </button>
    </div>
   </Fragment>
  )
}