import React, {Fragment} from 'react'
import AddPersonIcon from '../images/AddPersonIcon.svg'
import Clip from '../images/Clip.svg'
import UserMessage from './UserMesage'

export default function CommentList(props) {
  return (
   <Fragment>
   <div className="comment-list__container">
    <div className="comment-list__header">
      <h2>Comments</h2>
      <img src={AddPersonIcon} alt="" />
    </div>
    <div className="comment-list__body">
    { props.totalUsers.map(user => {
        return (   
        <UserMessage detal_user_info={user}/>
        )
       })
      }
    </div>
    <div className="comment-list__input-futter">
      <textarea className='comment-list__textarea' placeholder='Your message here... '></textarea>
      <input className='comment-list__input-image' type="image" src={Clip} />
    </div>
   </div>
  </Fragment>
  )
}